import * as React from "react";
import save from "shared/components/save";
import Defer from "shared/components/ui/Defer";
import { useFetcher } from 'shared/components/ajax';
import Navbar from "shared/components/ui/Navbar";
import { Button, Grid, Typography, withStyles } from "@material-ui/core";
// @ts-ignore
import MailIcon from 'shared/images/Icon Surat.png';
// @ts-ignore
import EcomplainIcon from 'shared/images/Icon Ecomplain.png';
// @ts-ignore
import EmergencyIcon from 'shared/images/Icon Emergency.png';
// @ts-ignore
import RubrikIcon from 'shared/images/Icon Demografi.png';
import { navigate } from "gatsby";

/**
 * @typedef {import('../../../../shared/types/userInfo').UserInfo} UserInfo
 */

/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    container: {
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
        '& .MuiGrid-container': {
            margin: 'auto 0',
        },
        '@media (min-width: 960px)': {
            '&  .MuiButton-root': {
                height: 300
            }
        }
    },
    button: {
        margin: 20,
        width: 'calc(100% - 40px)',
        '& .MuiButton-label': {
            flexDirection: 'column',
            display: 'flex',
        },
        '& img': {
            marginBottom: theme.spacing(1),
            objectFit: 'contain',
            width: '40px',
            height: '40px',
        }
    },
})

const Page = withStyles(styles)(({ classes }) => {
    /**
     * @type {UserInfo}
     */
    const data = useFetcher('/auth/info', (d) => {
        save.userInfo = d;
    });
    return (
        <Defer data={data}>
            {() => (
                <div className={classes.container}>
                    <Navbar />
                    <Grid container>
                        <Grid item xs={12}>
                            <Typography align="center" variant="h4" gutterBottom>
                                Admin Perangkat Desa {data.geolookup.kelurahan}
                            </Typography>
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <Button variant="outlined" className={classes.button} onClick={() => navigate('/desa/surat/')}>
                                <img src={MailIcon} alt="" />
                                <Typography gutterBottom>
                                    Admin Surat
                                </Typography>
                            </Button>
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <Button variant="outlined" className={classes.button} onClick={() => navigate('/desa/darurat/')}>
                                <img src={EmergencyIcon} alt="" />
                                <Typography gutterBottom>
                                    Admin Tanggap Darurat
                                </Typography>
                            </Button>
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <Button variant="outlined" className={classes.button} onClick={() => navigate('/desa/komplain/')}>
                                <img src={EcomplainIcon} alt="" />

                                <Typography gutterBottom>
                                    Admin Komplain
                                </Typography>
                            </Button>
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <Button variant="outlined" className={classes.button} onClick={() => navigate('/desa/info/')}>
                                <img src={RubrikIcon} alt="" />
                                <Typography gutterBottom>
                                    Admin Moderasi Layanan
                                </Typography>
                            </Button>
                        </Grid>
                    </Grid>
                </div>
            )}
        </Defer>
    );
})

export default Page;
